<template>
  <div class="root-container">

    <loading-overlay />

    <div ref="pageLoaderContainer">
      <pages-loader :can-show="showLoader"></pages-loader>
    </div>

    <landscape-component />

    <slide-header />

    <div class="container-fluid p-0">
      <router-view v-slot="{ Component }">
        <transition @enter="onEnter" @leave="onLeave" :css="false" mode="out-in">
          <component :is="Component" />
        </transition>
      </router-view>
    </div>
  </div>
</template>


<script setup>

import SlideHeader from './components/app-ui/SlideHeader.vue';
import PagesLoader from './components/app-ui/PagesLoader';
import LandscapeComponent from "./components/orbital/LandscapeComponent";
import LoadingOverlay from "@/components/app-ui/LoadingOverlay";

import { ref } from "vue";
import { transitionEnter, transitionLeave } from "@/utils/pageTransition";



const showLoader = ref(true);
const pageLoaderContainer = ref(null);


function onEnter() {
  transitionEnter(pageLoaderContainer.value, () => {
    showLoader.value = false
  });
}

function onLeave(el, done) {
  showLoader.value = true;
  transitionLeave(pageLoaderContainer.value, done);
}



</script>

