import {createRouter , createWebHistory} from 'vue-router'


const HomePage = () => import(/* webpackChunkName: "group-orbital" */'@/pages/HomePage');
const LocalisationPage = () => import('@/pages/LocalisationPage');
const PanoramaPage = () => import('@/pages/PanoramaPage');
const GaleriePage = () => import('@/pages/GaleriePage');
const OrbitalsList = () => import('@/pages/OrbitalsList');
const VillaOnePage = () => import(/* webpackChunkName: "group-orbital" */'@/pages/VillaOnePage');
const VillaTwoPage = () => import('@/pages/VillaTwoPage');
const VillaThreePage = () => import('@/pages/VillaThreePage');
const SearchPage = () => import('@/pages/SearchPage');
const LoginPage = () => import('@/pages/LoginPage');
const AdminPage = () => import('@/pages/AdminPage');


const routes = [
    { path: '/', name : 'home' , component: HomePage },
    { path: '/localisation', name : 'localisation' , component: LocalisationPage },
    { 
        path: '/orbitals',  
        component: OrbitalsList , 
        children: [
            {
                path: '', 
                redirect: { name: "startup" },
            },
            {
                path: 'villa-1', 
                component: VillaOnePage,
                name : "startup"
            },
            {
                path: 'villa-2', 
                component: VillaTwoPage
            },
            {
                path: 'villa-3', 
                component: VillaThreePage
            }
        ] 
    },
    { path: '/panorama',  name : 'panorama' , component: PanoramaPage },
    { path: '/galerie',  name : 'galerie' , component: GaleriePage },
    { path: '/recherche', name : 'search' , component: SearchPage },
    { path: '/login', name : 'login' , component: LoginPage },
    { 
        path: '/admin', 
        name : 'admin' , 
        component: AdminPage,
        meta: {
            requiresAuth : true
        }
    },

    { path: '/:pathMatch(.*)*', name: 'home', component: HomePage}
];

const router = createRouter({
    history: createWebHistory(),
    linkActiveClass: "active",
    routes,
});


router.beforeEach(async(to , from , next) => {
    if(to.matched.some(record => record.meta.requiresAuth)){
        const token = localStorage.getItem('token');

        if(token) {
            //check if tocken is valid
            return next();
        }
        return next('/login');
    }

    next();
});


export default router;