import { defineStore , storeToRefs } from 'pinia';
import { useAuthStore } from "@/stores/auth";
import { useAppDataStore } from "@/stores/appData";

import { ref , computed } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";

export const useRegionsStore = defineStore('regions', () => {

    const authStore = useAuthStore();

    const appDataStore = useAppDataStore();

    const {currentOrbital , currentLotInfo , colorTypes , defaultFilters} = storeToRefs(appDataStore);
    
    const router = useRouter();

    const regions = ref({});
    const filteredRegions = ref({});
    const regionId = ref("");
    const currentRegion = ref({});
    const currentType = ref("");
    const isError = ref(false);
    const isLoading = ref(false);
    const loadingMessage = ref("Loading");

    const HSHoverInfo = computed(() => {
        return currentLotInfo.value.HSHoverInfo;
    });
    const AllInfo = computed(() => {
        return currentLotInfo.value.AllInfo;
    });
    const titleInfoScheme = computed(() => {
        return currentLotInfo.value.titleInfo;
    });


    const titleInfo = computed(() => {
        const key = titleInfoScheme.value.normal;
        const a = currentRegion.value[key];
        return "APPT-" + _2digits(a);
    });
    const titleInfoSmall = computed(() => {
        const key = titleInfoScheme.value.small;
        const a = currentRegion.value[key];
        return "BLOC-" + _2digits(a);
    });

    const region = computed(() => {
        if(Object.keys(regions.value).length) {
            const reg = regions.value[regionId.value];
            currentRegion.value = reg;
            return reg;
        }
        return undefined;
    });

    const currentColor = computed(() => {
        if(Object.keys(colorTypes.value).length) {
            return colorTypes.value[currentType.value];
        }
        return undefined;
    });

    function _2digits(num) {
        const prefix = (num <= 9) ? "0" : "";
        return prefix + num;
    }


    function setRegion(id){
        regionId.value = id;
        currentRegion.value = regions.value[id];
        currentType.value = currentRegion.value.type;
    }

    function _beforeFetch(){
        regions.value = {};
        isLoading.value = true;
        loadingMessage.value = `Switching to ${currentOrbital.value.title}`;
    }

    /**
     * just for local test we fetch from public folder regions
     */
    async function fetchRegions() {
        _beforeFetch();
        try {
            const regionConfigFile = currentOrbital.value.regionConfigFile;
            const URL = `/config-files/regions/${regionConfigFile}.json`;
            const res = await axios.get(URL);
            regions.value = res.data;
            isError.value = false;
            isLoading.value = false;

        } catch (err) {
            console.log(err)
            isError.value = true;
            isLoading.value = false;
            const error = (err.response) ? err.response.data : undefined;
            if (error === undefined) return err.message;
            return error.message || err.response.statusText || err.message;
        }
    }

    /**
     * the real fetch method
     */
    /* async function fetchRegions() {
        _beforeFetch();
        try {
            const regionConfigFile = currentOrbital.value.regionConfigFile;
            const URL = `/get-regions/${regionConfigFile}`;
            const res = await axios.request({
                url: URL,
                method: 'GET',
                baseURL: process.env.VUE_APP_API_BASE_URL,
            });
            regions.value = res.data.data.regions;
            isError.value = false;
            isLoading.value = false;

        } catch (err) {
            console.log(err)
            isError.value = true;
            isLoading.value = false;
            const error = (err.response) ? err.response.data : undefined;
            if (error === undefined) return err.message;
            return error.message || err.response.statusText || err.message;
        }
    } */

    async function fetchAuthRegions() {
        _beforeFetch();
        try {
            const URL = `/list-appt/${currentOrbital.value.regionConfigFile}`;
            const token = authStore.getToken();
            const res = await axios.request({
                url: URL,
                method: 'GET',
                baseURL: process.env.VUE_APP_API_BASE_URL,
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });
            regions.value = res.data.data.regions;
            isError.value = false;
            isLoading.value = false;
            authStore.isAdmin = true;
        } catch (e) {
            isError.value = true;
            isLoading.value = false;
            authStore.isAdmin = false;
            router.push('login');
        }
    }

    async function editStatus(id , status) {
        isLoading.value = true;
        loadingMessage.value = `Edit status`;
        try {
            const token = authStore.getToken();
            const url = `${process.env.VUE_APP_API_BASE_URL}/edit-appt`;
            const response = await axios.post(url, {
                id,
                status: status,
                fileName : currentOrbital.value.regionConfigFile
            }, 
            {
                headers: {
                    'content-type': 'application/json',
                    'Authorization': `Bearer ${token}`
                }
            });
            const res = response.data;
            regions.value = res.data.regions;
            isError.value = false;
            isLoading.value = false;
            return res.message;
    
        } catch (err) {
            isError.value = true;
            isLoading.value = false;
            const error = (err.response) ? err.response.data : undefined;
            if (error === undefined) return err.message;
            return error.message;
        }
    }




    /**
     * Filter regions by given filters
     * @param {Object} filters 
     */
    function filterRegions(filters){
        filteredRegions.value = Object.fromEntries(
            Object.entries(regions.value).filter(item => {
                const v = item[1];
                const surface = v.surface;
                const type = v.type;
                const stage = v.etage;
                const status = v.statut;
                return _isValide(filters, surface, type, stage, status);
            })
        );
    }
    function _isValide(filters , surface, type, stage, status) {
        return (
            _isValideSurface(filters , surface) &&
            filters.types.includes(type) &&
            filters.stages.includes(Number(stage)) &&
            filters.status.includes(status)
        );
    }
    
    function _isValideSurface(filters , surface) {
        if (!filters.surface) return true;
        return (surface >= filters.surface.min && surface <= filters.surface.max)
    }


  
    return { 
        regions ,
        filteredRegions,
        regionId,
        currentRegion ,
        region,
        HSHoverInfo,
        AllInfo,
        titleInfoScheme,
        titleInfo , 
        titleInfoSmall , 
        defaultFilters,
        currentColor,
        isError,
        isLoading,
        loadingMessage,
        fetchRegions, 
        fetchAuthRegions,
        editStatus,
        setRegion,
        filterRegions
    }
});



