import { ref ,computed , watch } from "vue";
import { defineStore } from 'pinia';

import axios from "axios";
import AVAILABLE_ORBITALS from "/public/config-files/available-orbitals.json";
import ORBITALS_INFOS from "/public/config-files/orbitals-infos.json";

export const useAppDataStore = defineStore('appData', () => {

    const currentOrbital = ref(AVAILABLE_ORBITALS.villa1);
    const availableOrbitals = ref(AVAILABLE_ORBITALS);
        
    const _appData = ref({});
    const params = ref({});
    const hsParams = ref({});
    const colorTypes = ref({});
    const defaultFilters = ref({});
    const defaultStatus = ref([]);
    const status = ref([]);
    const surfaceMinMax = ref({});

    const orbitalConfig = ref({});

    const currentUID = computed(() => {
        const uid = ORBITALS_INFOS[currentOrbital.value.id].uniqueId;
        return uid;
    });

    const currentLotInfo = computed(() => {
        const orbital = currentOrbital.value;
        return ORBITALS_INFOS[orbital.id];
    });

    watch(currentOrbital , () => {
        _fetchOrbitalConfig().then(() => {
            setAppData(orbitalConfig.value);
        });
    },{
        immediate: true 
    });
    
    function setCurrentOrbital(id){
        const config = AVAILABLE_ORBITALS[id];
        if(config === "undefined") return;
        currentOrbital.value = config;
    }

    function setAppData(data){
        _appData.value = data;
        _configureData();
    }


    function _configureData(){
        const data = _appData.value;
        params.value = data.params;
        hsParams.value = data.hotspots;
        colorTypes.value = hsParams.value.color_types;
        defaultFilters.value = hsParams.value.default_filters;
        defaultStatus.value = defaultFilters.value.status;
        status.value = hsParams.value.status;
        surfaceMinMax.value = hsParams.value.surface;
    }

    async function _fetchOrbitalConfig() {
        try {
            const response = await axios.get(`/config-files/${currentOrbital.value.orbitalConfigFile}`);
            orbitalConfig.value = response.data;
          } catch (error) {
            console.error('Error loading JSON:', error);
          }
    }
    async function fetchHSConfig() {
        try {
            const response = await axios.get(`/config-files/hotspots/${currentOrbital.value.HSConfigFile}`);
            return response.data;
          } catch (error) {
            console.error('Error loading JSON:', error);
          }
    }

    return {
        currentOrbital,
        availableOrbitals,
        params,
        hsParams,
        colorTypes,
        defaultFilters,
        defaultStatus,
        status,
        surfaceMinMax,
        currentLotInfo,
        orbitalConfig,
        currentUID,
        setAppData,
        setCurrentOrbital,
        fetchHSConfig
    }

});