<template>
    <div class="landscape position-fixed w-100 h-100 d-flex flex-column justify-content-center align-items-center"
        v-if="showLandscape">
        <h2 class="landscape-title mb-5 p-2">Changer l'orientation de l'appareil pour une meilleur experience</h2>
        <div class="phone">
            <svg xmlns="http://www.w3.org/2000/svg" width="5em" height="5em" fill="currentColor"
                class="bi bi-phone-landscape" viewBox="0 0 16 16">
                <path
                    d="M1 4.5a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v6a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1v-6zm-1 6a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2v-6a2 2 0 0 0-2-2H2a2 2 0 0 0-2 2v6z" />
                <path d="M14 7.5a1 1 0 1 0-2 0 1 1 0 0 0 2 0z" />
            </svg>
        </div>
    </div>
</template>

<script setup>

import { ref, onMounted, onUnmounted } from "vue";

const showLandscape = ref(false);

function onWindowResized() {
    showLandscape.value = document.documentElement.clientWidth < 500;
}

onMounted(() => {
    onWindowResized();
    window.addEventListener('resize', onWindowResized);
});

onUnmounted(() => {
    window.removeEventListener('resize', onWindowResized);
});

</script>


<style lang="scss" scoped>
.landscape {
    background-color: var(--app-primary-color);
    z-index: 260;

    .landscape-title {
        color: #ffffff;
        text-align: center;
        font-size: .85em;
        text-transform: uppercase;
        letter-spacing: .2em;
        line-height: 2;
        font-weight: 400;
    }

    .phone {
        transform: rotateZ(90deg);
        animation-name: phoneRotate;
        animation-duration: 2.5s;
        animation-iteration-count: infinite;
    }
}


@keyframes phoneRotate {
    0% {
        transform: rotateZ(90deg);
    }

    40% {
        transform: rotateZ(0deg);
    }

    70% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(90deg);
    }


}
</style>